// components/blog/BlogTableOfContents.tsx
'use client';

import { useState } from 'react';

interface TableOfContentsProps {
  sections: string[];
}

export function BlogTableOfContents({ sections }: TableOfContentsProps) {
  const [activeSection, setActiveSection] = useState(-1); // No section active by default

  const handleSectionClick = (index: number) => {
    setActiveSection(index);
  };

  return (
    <div 
      className="mb-[60px]"
      style={{
        background: '#F8F9FA',
        boxShadow: '-4px 4px 12px rgba(0, 0, 0, 0.1)',
        borderRadius: '11px',
        padding: '22px 15px',
        minHeight: '1053px'
      }}
    >
      <h3 
        className="mb-[22px] text-center"
        style={{
          fontFamily: 'Sequel Sans',
          fontWeight: 415,
          fontSize: '25px',
          lineHeight: '32px',
          color: '#000000'
        }}
      >
        Table of Contents
      </h3>
      
      <div className="space-y-[15px]">
        {sections.map((section, index) => (
          <div 
            key={index}
            onClick={() => handleSectionClick(index)}
            className="relative cursor-pointer transition-all"
            style={{
              background: activeSection === index ? 'linear-gradient(90deg, #6B8BF5 0%, #8BA5FF 100%)' : '#EFF2FF',
              borderRadius: '17px',
              padding: '20px 25px',
              minHeight: '59px',
              filter: 'drop-shadow(-2px 1px 4px rgba(0, 0, 0, 0.08))',
              display: 'flex',
              alignItems: 'center',
              border: activeSection === index ? 'none' : '1px solid #E6E9F4'
            }}
            onMouseEnter={(e) => {
              if (activeSection !== index) {
                e.currentTarget.style.background = '#E8ECFC';
              }
            }}
            onMouseLeave={(e) => {
              if (activeSection !== index) {
                e.currentTarget.style.background = '#EFF2FF';
              }
            }}
          >
            <p 
              style={{
                fontFamily: 'Sequel Sans',
                fontWeight: activeSection === index ? 500 : 415,
                fontSize: '16px',
                lineHeight: '19px',
                color: activeSection === index ? '#FFFFFF' : '#000000',
                margin: 0
              }}
            >
              {section}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}